import React from 'react';
import { Layout } from './layout.styles';
import { useDeviceDetect } from '../hooks/use-device-detect/use-device-detect.hook';
import { Header as MobileHeader } from '../app/home/mobile-header/header.component';
import { MobileMenuStateProvider } from '../providers/mobile-menu.provider';
import { Menu } from '../ui/mobile-menu/menu.component';
import { Header as DesktopHeader } from '../app/home/desktop-header/header.component';
import { ThemeProvider } from 'styled-components';
import { theme } from '../theme/theme.config';

export const LayoutProvider = ({ children }: { children: React.ReactNode }) => {
  const { isMobile } = useDeviceDetect();

  return (
    <Layout>
      <style jsx global>
        {`
          html,
          body {
            margin: 0;
            padding: 0;
            font-family: 'Montserrat', sans-serif;
            background-color: #f9f9f9;
            position: fixed;
            // width: 100%;
            top: 0px;
            min-height: 100%;
            height: 100%;
            overflow: hidden;
            -webkit-overflow-scrolling: touch;
          }
        `}
      </style>
      <ThemeProvider theme={theme}>
        <React.Fragment>
          {isMobile ? (
            <MobileMenuStateProvider>
              <React.Fragment>
                <MobileHeader />
                <Menu />
                <div>{children}</div>
              </React.Fragment>
            </MobileMenuStateProvider>
          ) : (
            <div>
              <DesktopHeader />
              <div>{children}</div>
            </div>
          )}
        </React.Fragment>
      </ThemeProvider>
    </Layout>
  );
};
